.registration-for-lesson {
  background: url("../img/footer-yoga-mobile.png");
  background-size: cover;
  background-repeat: no-repeat;
  padding: 70px 35px;
}

.registration-for-lesson__title {
  font-size: 20px;
  line-height: 27px;
  text-align: center;
  margin-bottom: 25px;
}

.registration-for-lesson__input {
  font-size: 14px;
  line-height: 14px;
  width: 100%;
  background-color: inherit;
  border: none;
  border-bottom: 1px solid $main-color;
  color: $main-color;
  padding: 15px 0;
  margin-bottom: 20px;
    
  &::-webkit-input-placeholder { color: rgba(200, 192, 219, 0.9); }
  &::-moz-placeholder { color:rgba(200, 192, 219, 0.8); }
  &:-ms-input-placeholder { color:rgba(200, 192, 219, 0.8); }
}

.registration-for-lesson__btn {
  cursor: pointer;
  width: 100%;
  text-align: center;
  padding: 24px 0;
  background-color: #E65C7B;
  border: none;
  color: #ffffff;
  margin-top: 35px;

  &:hover {
    opacity: 0.8;
  }
}

@media (min-width: $sm-width) {
  .registration-for-lesson {
    background: url("../img/footer-yoga-tablet.png");
    padding: 70px 100px;
  }
}

@media (min-width: $tablet-width) {
  .registration-for-lesson {
    background-image: none;
    width: 50%;
    padding: 70px 50px;
  }
}
