.feedback-contacts__social-block {
  display: flex;
  justify-content: flex-start;
  padding: 0;

  & li:first-child {
    margin-right: 8px;
  }
}

.feedback-contacts__contacts {
  background-color: $footer-color;
  padding: 50px 35px 30px 35px;
}

@media (min-width: $sm-width) {
  .feedback-contacts__contacts {
    background-color: $footer-color;
    padding: 50px 35px 30px 100px;
  }
}


@media (min-width: $tablet-width) {
  .feedback-contacts__wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 70px 10px;
  }

  .feedback-contacts {
    background: url("../img/footer-yoga-desktop.png");
    background-size: cover;
    background-repeat: no-repeat;
  }

  .feedback-contacts__registration {
    order: 2;
  }

  .feedback-contacts__contacts {
    width: 50%;
    background-color: inherit;
    padding: 50px 35px 30px 35px;
  }
}

@media (min-width: $desktop-width) {
  .feedback-contacts__wrapper {
    width: $desktop-width;
    margin: 0 auto;
  }

  .feedback-contacts__contacts {
    width: 50%;
    background-color: inherit;
    padding: 50px 50px 30px ;
  }
}