.copiright {
  @include list-style-off;

  li {
    color: rgba(255, 255, 255, 0.5);
    margin-bottom: 25px;
    a {
      color: inherit;
    }
  }
}

@media (min-width: $tablet-width) {
  .copiright {
    display: flex;
    margin-right: 50px;
   li {
      margin-bottom: 0;

      &:last-child {
        text-align: right;
      }
    }
  }
}

@media (min-width: $desktop-width) {
  .copiright {
    margin-left: 30px;
    margin-right: 70px;
  }
}