.navbar {
  padding-left: 0;
}

.navbar-toggler {
  padding-left: 0;
}

.navbar-toggler-icon {
  display: block;
  margin-bottom: -1px;
  height: 8px;
  width: 24px;
  border-top: 1px solid $main-color;
  border-bottom: 1px solid $main-color;

  &--bottom {
    width: 14px;
    border-top: none;
  }
}

.nav-link {
  margin-top: 20px;
  color: $main-color;
  font-size: 26px;
  line-height: 10px;
  transition: 0.2s;

  &:hover {
    color: $main-color;
    opacity: 0.8;
  }
}

.navbar__description {
  font-size: 16px;
  line-height: 10px;
  color: $main-color;
}
