.small-title {
  color: $main-color;
  width: fit-content;
  margin: 0 auto;
   
  a {
    font-weight: bold;
    font-size: 18px;
    color: $main-color;
  }
}

@media (min-width: $tablet-width) {
  .small-title {   
    a {
      font-size: 21px;
    }
  }
}

@media (min-width: $desktop-width) {
  .small-title {   
    a {
      font-size: 30px;
    }
  }
}