.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

body {
  margin: 0;
  font-family: "Geometria", "Lucida Console", "Monaco", sans-serif;
  font-weight: 300;
  color: $main-color;
  background-color: $body-color;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}
