.contacts {
  @include list-style-off;
  font-size: 14px;
  line-height: 20px;
  color: #ffffff;

  & a {
    color: #ffffff;
  } 
}

@media (min-width: $tablet-width) {
  .contacts {
    color: $main-color;

    &:first-child {
      margin-right: 60px;
    }

    & a {
      color: $main-color;
    } 
  }
}