.reviews {
  padding-bottom: 70px;
  padding-right: 30px;
  padding-left: 30px;
}

.reviews__list {
  @include list-style-off;
  margin-bottom: 30px;
}

.reviews__item:not(:first-child) {
  display: none;
}

.reviews__title {
  margin-bottom: 40px;
}

.reviews__item {
  @include box-shadow;
  position: relative;
  padding: 60px 35px 30px 35px;
  background-color: #ffffff;

  &::before {
    content: '"';
    position: absolute;
    top: 45px;
    left: 12px;
    font-family: "Coiny","Monaco", cursive;
    font-size: 80px;
    line-height: 15px;
    color: rgba(142, 128, 169, 0.39);
  }
}

.reviews__body {
  margin-bottom: 30px;
}

.reviews__text {
  font-size: 10px;
  line-height: 15px;
}

.reviews__descrip {
  padding: 0 0 0 13px;
  flex-direction: column;
  box-shadow: none;

  &:hover {
    cursor: auto;
    box-shadow: none;
  }
}

.reviews__card {
  display: flex;
}

.reviews__card-title {
  font-size: 12px;
  line-height: 20px;
  margin-bottom: 0;
}

.reviews__social {
  padding-left: 0;
  margin-left: -8px;
  margin-top: -5px;
  display: flex;
  
  a:hover {
    background-color: inherit;
    svg {
      fill: $contrast-color;
    }
  }
}

.reviews__control {
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: end;
}

.reviews__btn {
  width: 31px;
  height: 16px;
}

.reviews__breadcrumbs {
  font-size: 12px;
  line-height: 20px;
}

@media (min-width: $sm-width) {
  .reviews {
    padding-bottom: 150px;
  }

  .reviews__list {
    margin-bottom: 45px;
  }

  .reviews__item:not(:first-child) {
    display: block;
  }

  .reviews__item {
    padding: 72px 57px 30px 87px;
    margin-bottom: 20px;

    &::before {
      top: 60px;
      left: 80px;
    }
  }

  .reviews__image {
    position: relative;
    top: 0;
    left: 0;
    width: 51px;
    height: 51px;
    margin-bottom: 0;
  }

  .reviews__control {
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: end;
    flex-direction: row;
  }

  .reviews__btn {
    width: 31px;
    height: 16px;
  }

  .reviews__breadcrumbs {
    font-size: 12px;
    line-height: 20px;
  }
}

@media (min-width: $tablet-width) {
  .reviews__list {
    display: flex;
    justify-content: space-between;
  }

  .reviews__item:first-child {
    margin-right: 20px;
  }

  .reviews__control {
    padding-left: 30px;
    padding-right: 30px;
    position: absolute;
    top: 203px;
    left: 0;
    justify-content: space-between;
    align-items: end;
  }

  .reviews__btn {
    width: 45px;
    height: 30px;
    background-color: $main-color;
    background-repeat: no-repeat;
    background-position: center;
    
    &--prev {
      background-image: url("../img/left.svg");

      &:hover {
        background-color: $body-color;
        background-image: url("../img/left-btn.svg");
      }
    }

    &--next {
      background-image: url("../img/right.svg");

      &:hover {
        background-color: $body-color;
        background-image: url("../img/right-btn.svg");
      }
    }
  }

  .reviews__breadcrumbs {
    display: none;
  }

  .reviews {
    position: relative;
  }
}