.footer {
  margin-top: -1px;
  background-color: $footer-color;
  padding-bottom: 50px;
  padding-left: 35px;
}

@media (min-width: $sm-width) {
  .footer {
    justify-content: flex-start;
    margin-left: 0;
  }

  .footer__wrapper {
    width: 80%;
    margin: 0 auto;
  }
}

@media (min-width: $tablet-width) {
  .footer {
    padding-top: 30px;
    padding-bottom: 30px; 
    justify-content: space-around;
  }

  .footer__wrapper {
    width: 100%;
  }
}

@media (min-width: $desktop-width) {
  .footer__wrapper {
    width: $desktop-width;
  }
}