.social-block {
  @include list-style-off;
  padding-left: 20px;
}

.social-block__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25px;
  height: 25px;
  border-radius: 20px;
  margin-left: 3px;
  transition: 0.3s;
  svg {
    fill: $main-color;;
  }

  &:hover {
    background-color: $main-color;
    svg {
      fill: #ffffff;
    }
  }

  &--contacts {
    svg {
      fill: #ffffff;
      width: 25px;
      height: 15px;
    }

    &:hover {
      background-color: inherit;
      svg {
        fill: $contrast-color;
      }
    }
  }
}

.social-block__icon {
  &--contacts {
    width: 10px;
    height: 21px;
  }
}

@media (min-width: $tablet-width) {
  .social-block__link {
  &--contacts {
    svg {
      fill: $main-color;
    }

    &:hover {
      svg {
        fill: #ffffff;
      }
    }
  }
}

}

