.team-card {
  background-color: #ffffff;
}

.team-card__descrip {
  @include box-shadow;
  width: 100%;
  font-size: 10px;
  display: flex;
  justify-content: space-between;
  padding: 15px 20px 9px 15px;

  &:hover {
    cursor: pointer;
    @include hover-box-shadow;
  }
}

.team-card__title {
  width: 80%;
  font-size: 10px;
  line-height: 15px;
  color: #e65c7b;
}

.team-card__social {
  margin-bottom: 0;
}

@media (min-width: $sm-width) {
  .team-card {
    position: relative;
  }

  .team-card__image {
    position: absolute;
    width: 91px;
    height: 91px;
    margin-bottom: 30px;
    top: -35px;
    left: -20px;
  }

  .team-card__descrip {
    padding: 11px 11px 11px 84px;
  }

  .team-card__social {
    padding: 0;
  }

  .team-card__title {
    margin-bottom: 0;
  }

  .team-card__details {
    width: fit-content;
    margin: 0 auto;
    color: $main-color;

    &:hover {
      color: inherit;
    }
  }
}

@media (min-width: $tablet-width) {
  .team-card__social {
    display: flex;
  }
}
