.slider__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 16px;
  height: 31px;
  background-color: transparent;
  border: 1px solid $main-color;

  &--prev {
    margin-left: -25px;
    background: url("../img/left-btn.svg") no-repeat center;

    &:hover {
      background: url("../img/left.svg") no-repeat $main-color center;
    }
  }

  &--next {
    margin-right: -25px;
    background: url("../img/right-btn.svg") no-repeat center;

    &:hover {
      background: url("../img/right.svg") no-repeat $main-color center;
    }
  }
}

.slider__breadcrumbs {
  margin-bottom: 0;
  margin-right: 10px;
  margin-left: 10px;
}

.slider__control {
  margin-bottom: -55PX;
}

@media (min-width: $sm-width) {
  .slider {
    display: flex;
    flex-direction: column;
  }

  .slider__control {
    order: 10;
  }

  .slider__control {
    margin-bottom: 0;
    margin-right: 30px;
  }

  .slider__btn {
    width: 30px;
    height: 20px;
  }
}