.subscriptions {
  background: url("../img/middle-cloud-mobile.png") no-repeat 0 -170px;
  background-size: cover;
}

.subscriptions__container {
  padding-left: 50px;
  padding-right: 50px;
}

.subscriptions__duration {
  padding: 0;
}
.subscriptions__duration-btn {
  font-size: 12px;
  line-height: 25px;
  color: $contrast-color;
  background-color: transparent;
  border: none;

  &--active {
    border-bottom: 1px solid $contrast-color;
  }

  &:hover {
    cursor: pointer;
    opacity: 0.7;
    border-bottom: 1px solid $contrast-color;
  }
}

.subscriptions__title-main {
  margin-bottom: 40px;
}

.subscriptions__list {
  padding: 0 15px 0 15px;
}

.subscriptions__item {
  @include box-shadow;
  background-color: #ffffff;
  padding: 30px 50px;
  margin-bottom: 30px;
}

.subscription__title {
  font-size: 16px;
  font-weight: bold;
}

.subscription__description {
  width: 70%;
  font-size: 10px;
  line-height: 15px;
  color: $contrast-color;
  margin: 20px auto;
}

.subscription__price {
  font-size: 18px;
  font-weight: bold;
  line-height: 20px;
}

.subscription__btn {
  cursor: pointer;
  width: 100%;
  min-height: 34px;
  color: #ffffff;
  font-size: 10px;
  line-height: 20px;
  background-color: $main-color;
  border: none;

  &:hover {
    background-color: $contrast-color;
  }
}

@media (min-width: $sm-width) {
  .subscriptions {
    background: url("../img/middle-cloud-tablet.png") no-repeat 0 150px;
  }

  .subscriptions__item {
    margin-right: 20px;
  }
}

@media (min-width: $tablet-width) {
  .subscriptions {
    background: url("../img/middle-cloud-desktop.png") no-repeat 0 -112px;
    background-size: cover;
  }
  .subscriptions__duration {
    margin-right: 40px;
    margin-bottom: 40px;
    &:last-child {
      margin-right: 0;
    }
  }

  .subscriptions__title-main {
    margin-bottom: 25px;
  }

  .subscription__title {
    font-size: 18px;
  }

  .subscription__btn {
    display: none;
  }

  .subscriptions__item {
    max-height: 185px;
    padding: 30px 35px;

    &:hover {
      max-height: 294px;
      margin-bottom: -21px;

      .subscription__btn {
        display: block;
      }
    }
  }

  .subscription__description {
    width: 103%;
  }
}

@media (min-width: 992px) {
  .subscriptions__container {
    max-width: 80%;
    margin: 0 auto 0px;
  }
}

@media (min-width: $desktop-width) {
  .subscription {
    margin-bottom: 160px;
  }
  .subscription__description {
    font-size: 12px;
  }

  .subscription__price {
    font-size: 30px;
    margin-bottom: 36px;
  }

  .subscriptions__item {
    max-height: 230px;
    padding: 60px 54px 54px 54px;
  }

  .subscription__btn {
    min-height: 52px;
  }
}