.logo-block__slogan {
  margin: 0;
  color: #8b7eac;
  font-family: "Caveat","Lucida Console","Monaco",sans-serif;
  font-size: 18px;
  opacity: 0.7;
}

.logo-block__logo {
  width: 100%;

  img {
    display: block;
    margin: 0 auto;
  }
}

.logo-block__button {
  position: relative;
  margin: 0 auto;
  padding-left: 37px;
  color: $contrast-color;
  font-size: 10px;
  transition: 0.3s;

  &::before {
    content: "";
    position: absolute;
    width: 30px;
    height: 26px;
    left: 0px;
    top: 0px;
    background: url("../img/right.svg") no-repeat 13px 4px $contrast-color;
    background-size: 7px 18px;
  }

  &:hover{
    color: $contrast-color;

    &::before {
      transition: 0.5s;
      background-position: 21px 4px;
    }
  }
}


@media (min-width: $tablet-width) {
  .logo-block__slogan {
    font-size: 24px;
  }
}

@media (min-width: $desktop-width) {
  .logo-block__slogan {
    font-size: 40px;
  }

  .logo-block__button {
    font-size: 14px;
    padding-left: 37px;

    &::before {
      left: -28px;
      top: -3px;
      width: 56px;
      height: 48px;
      background-position: 25px 14px;
      background-size: 11px;
    }

    &:hover{

      &::before {
        background-position: 40px 13px;
      }
    }
  }
}