.header {
  padding-top: 20px;
  padding-bottom: 70px;
  background: url("../img/main-cloud-mobile.jpg");
  background-size: cover;
}

.header__adress {
  display: block;
  margin-top: 10px;
  font-size: 16px;
  color: $main-color;

  &:hover {
    color: $main-color;
  }
}

.header__logo-block {
  margin-top: 100px;
  margin-bottom: 130px;
}

@media (min-width: $tablet-width) {
  .header {
    padding-top: 40px;
    padding-bottom: 60px;
    background: url("../img/main-cloud-tablet.jpg");
    background-size: cover;
  }
}

@media (min-width: $desktop-width) {
  .header {
    padding-top: 75px;
    padding-bottom: 148px;
    background: url("../img/main-cloud-desktop.jpg");
    background-size: cover;
  }
}
