.directions {
  background: url("../img/top-pattern-mobile.png") no-repeat;
  background-position: 0px 100px;
  margin-bottom: 70px;
}

.directions__wrapper {
  margin: 0 auto;
}

.directions__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.directions__item {
  margin-bottom: 30px;
  max-width: 250px;
}

.directions__title {
  margin: 40px auto;
}

.direction__description-title {
  padding: 20px;
}

.directions__description-block {
  @include box-shadow;
  display: flex;
  background-color: #ffffff;
}

.directions__class-schedule {
  margin-bottom: 8px;
  font-size: 10px;

  span {
    display: block;
  }
}

.directions__subtitle {
  font-size: 12px;
  font-weight: bold;
}

.directions__teacher {
  display: block;
  width: 70%;
  font-size: 10px;
  color: $contrast-color;

  &:hover {
    color: $contrast-color;
  }
}

.directions__btn {
  width: 49px;
}

@media (min-width: $sm-width) {
  .directions {
  background: url("../img/top-pattern-tablet.png") no-repeat;
  background-position: 0px 0px;
}

  .directions__description-block {
    width: 100%;
  }

  .directions__item {
    position: relative;
    display: flex;
    width: 100%;
    max-width: none;
    margin-bottom: 20px;

    img {
      width: 150px;
      height: 120px;
    }
  }

  .directions__btn {
    margin-left: auto;
  }

  .direction__description-title {
    padding: 15px;
    padding-left: 20px;
  }

  .directions__btn-more {
    @include box-shadow;
    margin-top: 20px;
    min-height: 42px;
    font-size: 10px;
    color: $main-color;
    background-color: #ffffff;
    border-radius: 0;
    text-align: center;

    &:hover {
      @include hover-box-shadow;
    }
  }
}

@media (min-width: $tablet-width) {
  .directions {
    margin-bottom: 110px;
  }

  .directions__btn {
    position: absolute;
    width: 30px;
    height: 20px;
    right: 25px;
    bottom: 20px;
  }

  .directions__item--first {
    flex-direction: column;
    margin-bottom: 0;

    img {
     width: 100%;
     height: 100%;
    }

    .directions__btn {
      position: static;
      width: 48px;
      height: 130px;
    }
    
    .direction__description-title {
      padding: 20px;
    }
  }
}

@media (min-width: 992px) {
  .directions { 
    max-width: 80%;
    margin: 0 auto;
    margin-bottom: 70px;
  }
}

@media (min-width: $desktop-width) {
  .directions__item {
    img {
      width: 220px;
      height: 180px;
    }
  }

  .directions__subtitle {
    font-size: 16px;
  }

  .direction__description-title {
    padding: 33px;
  }

  .directions__class-schedule {
    font-size: 12px;
  }

  .directions__teacher {
    font-size: 12px;
  }

  .directions__btn {
    width: 45px;
    height: 30px;
  }

  .directions__item--first {
    img {
     width: 100%;
     height: 100%;
    }

    .directions__btn {
      width: 64px;
      height: 175px;
    }

    .direction__description-title {
      padding-top: 42px;
      padding-left: 45px;
      padding-bottom: 26px;
    }
  }
}

