.team {
  font-size: 10px;
  line-height: 20px;
  margin-bottom: 60px;
  background: url("../img/pattern-bottom-tablet.png") no-repeat 0 -60px;
}

.team__wrrapper {
  padding-left: 20px;
  padding-right: 20px;
}

.team__title {
  font-weight: bold;
  margin-bottom: 40px;
}

.team__slider {
  margin-bottom: 25px;
}

.team__experience {
  color: $contrast-color;
  display: block;
  margin-bottom: 20px;
}

.team__description {
  padding-left: 30px;
  padding-right: 30px;
}

@media (min-width: $sm-width) {
  .team {
    background: url("../img/pattern-bottom-tablet.png") no-repeat 0 -60px;
  }
  
  .team__card {
    margin-bottom: 57px;
  }

  .team__big-title {
    margin-bottom: 10px;
    margin-right: auto;
    margin-left: 0;
    font-weight: bold;
  }

  .team__slider {
    padding-top: 37px;
  }
}

@media (min-width: $tablet-width) {
  .team {
    background-image: none;
  }
}